window.dom = {
    create(string) {
        const container = document.createElement('template');
        container .innerHTML = string.trim();
        return container.content.firstChild;
    },
    after(node,node2){
        console.log(`node2${node2}`)
        node.parentNode.insertBefore(node2,null);
        // å¦‚æžœ referenceNode ä¸º null åˆ™ newNode å°†è¢«æ’å…¥åˆ°å­èŠ‚ç‚¹çš„æœ«å°¾ã€‚
    },
    before(node,node2){
        console.log(`node2${node2}`)
        node.parentNode.insertBefore(node2,node);

    },
    append(parent,node){
        parent.appendChild(node);
    },
    wrap(node,parent){//æ–°å¢žparent
        dom.before(node,parent);//å°†parentæ’åˆ°nodeå‰é¢
        dom.append(parent,node);//ç„¶åŽå°†nodeåŠ å…¥åˆ°parentä¸­ï¼Œé‚£ä¹ˆnodeä¼šä»ŽåŽŸæ¥çš„ä½ç½®ä¸­æ–­å¼€
    },

    remove(node){//ç§»é™¤nodeèŠ‚ç‚¹
        node.parentNode.removeChild(node);
        return node;//é˜²æ­¢ç§»é™¤ä¹‹åŽè¿˜è¦ç”¨åˆ°ï¼Œæ‰€ä»¥å¯ä»¥è¿”å›žå›žåŽ»ã€‚

    },
    empty(node){//æ¸…ç©ºnodeèŠ‚ç‚¹é‡Œé¢çš„å…ƒç´ 
        const arry = [];
        let x = node.firstChild;
        console.log(x);
        while(x){
            arry.push(dom.remove(node.firstChild));
            x = node.firstChild;
        }
        return arry;
    },
    attr(node,name,value){//é‡è½½
        if(arguments.length ===3){//è®¾ç½®èŠ‚ç‚¹nodeçš„nameå±žæ€§å€¼ä¸ºvalue
            node.setAttribute(name,value);
        }else if(arguments.length){//èŽ·å–nodeèŠ‚ç‚¹nameå±žæ€§çš„å€¼ã€‚
            return node.getAttribute(name);
        }
    },
    text(node,string){
        if(arguments.length === 2){
            if('innerText' in node){//é€‚é…
                node.innerText = string;
            }else{
                node.textConent = string;
            }
        }else if (arguments.length === 1){
            if('innerText' in node){
               return node.innerText ;
            }else{
               return node.textConent ;
            }
        }

    },
    html(node,string){
        if(arguments.length === 2){
         node.innerHTML = string;
        }else if(arguments.length ===1){
            return node.innerHTML;
        }
  
   },

   style(node,name,value){
    if(arguments.length === 3){
        node.style[name] = value;
       }else if(arguments.length ===2){
           return node.style[name];
       }else if (name instanceof Object){
        const obj = name;
        for(let key in obj){
            node.style[key] = obj[key];
        }
       }
      
   },
   class:{
        add(node,className){
            node.classList.add(className);
        },
        remove(node,className){
            node.classList.remove(className);
        },
        has(node,className){
            return node.classList.contains(className);
        }   

    },
    on(node,event,fn){//æ·»åŠ ç›‘å¬
        node.addEventListener(event,fn);
    },
    off(node,event,fn){//ç§»é™¤ç›‘å¬
        node.removeEventListener(event,fn);
    },
    find(selector,scope){//èŒƒå›´æŸ¥æ‰¾
        return (scope||document).querySelectorAll(selector);

    },
    parent(node){
        return node.parentNode;
    },
    children(node){
        return node.children;
    },
    siblings(node){//æ‰¾åˆ°nodeçš„å…„å¼ŸèŠ‚ç‚¹
      return  Array.from(node.parentNode.children)
      .filter(n=>n!=node);
    },
    next(node){
        let x = node.nextSibing
        while(x&& x.nodeType ===3){//ç±»åž‹ä¸ºå®žé™…æ–‡å­—textæ—¶æ‰¾ä¸‹ä¸€ä¸ªèŠ‚ç‚¹
            x = x.nextSibing;
        }
        return x;
    },
    previous(node){
        let x = node.previousSibling
        while(x && x.nodeType === 3){
            x = x.previousSibling;
        }
        return x;
    },
    each(nodeList,fn){
        for(let i= 0; i < nodeList.length;i++){
            fn.call(null,nodeList[i]);
        }
    },
    index(node){
        const list = dom.children(node.parentNode);
        for(let i =0 ;i <list.length;i++){
            if(list[i] === node){
                return i;
            }
        }
    }

}